// src/pages/hr/jobs.js
import { useState, useEffect } from "react";
import { supabase } from "lib/supabase";
import { useRouter } from "next/router";

export default function HRJobBoard() {
    const [title, setTitle] = useState("");
    const [description, setDescription] = useState("");
    const [file, setFile] = useState(null);
    const [expiresOn, setExpiresOn] = useState("");
    const [jobs, setJobs] = useState([]);
    const router = useRouter();

    useEffect(() => {
        // Check if authenticated
        if (!localStorage.getItem("hr_session")) {
            router.push("/hr/login");
        } else {
            fetchJobs();
        }
    }, [router]);

    const fetchJobs = async () => {
        const { data, error } = await supabase.from("job_openings").select("*");
        if (error) console.error("Error fetching jobs:", error);
        else setJobs(data);
    };

    const handleFileChange = (e) => {
        setFile(e.target.files[0]);
    };

    const handleSubmit = async (e) => {
        e.preventDefault();
        let fileUrl = null;

        // Placeholder for file upload logic (use your driveUtils.js)
        if (file) {
            fileUrl = "https://drive.google.com/example"; // Mock URL
        }

        const { error } = await supabase.from("job_openings").insert([
            {
                title,
                description: description || null,
                file_url: fileUrl || null,
                expires_on: expiresOn,
            },
        ]);

        if (error) console.error("Error submitting job:", error);
        else {
            setTitle("");
            setDescription("");
            setFile(null);
            setExpiresOn("");
            fetchJobs();
        }
    };

    return (
        <div className="min-h-screen p-6 bg-gray-100">
            <h2 className="text-2xl font-bold mb-6 text-center">HR Job Board</h2>
            <form onSubmit={handleSubmit} className="bg-white p-6 rounded-lg shadow-lg max-w-md mx-auto mb-8">
                <div className="mb-4">
                    <label className="block text-sm font-medium mb-1">Job Title</label>
                    <input
                        type="text"
                        value={title}
                        onChange={(e) => setTitle(e.target.value)}
                        className="w-full p-2 border rounded-lg"
                        required
                    />
                </div>
                <div className="mb-4">
                    <label className="block text-sm font-medium mb-1">Description (Optional)</label>
                    <textarea
                        value={description}
                        onChange={(e) => setDescription(e.target.value)}
                        className="w-full p-2 border rounded-lg"
                    />
                </div>
                <div className="mb-4">
                    <label className="block text-sm font-medium mb-1">Upload File (PDF/DOCX, Optional)</label>
                    <input
                        type="file"
                        accept=".pdf,.docx"
                        onChange={handleFileChange}
                        className="w-full p-2 border rounded-lg"
                    />
                </div>
                <div className="mb-4">
                    <label className="block text-sm font-medium mb-1">Expires On</label>
                    <input
                        type="date"
                        value={expiresOn}
                        onChange={(e) => setExpiresOn(e.target.value)}
                        className="w-full p-2 border rounded-lg"
                        required
                    />
                </div>
                <button type="submit" className="w-full p-2 bg-[#f05d23] text-white rounded-lg hover:bg-[#d94f1e]">
                    Submit Job
                </button>
            </form>

            <div className="max-w-4xl mx-auto">
                <h3 className="text-xl font-bold mb-4">Current Job Openings</h3>
                {jobs.map((job) => (
                    <div key={job.id} className="bg-white p-4 rounded-lg shadow-md mb-4">
                        <h4 className="text-lg font-semibold">{job.title}</h4>
                        {job.description && <p>{job.description}</p>}
                        {job.file_url && <a href={job.file_url} className="text-[#f05d23] underline">View File</a>}
                        <p className="text-sm text-gray-500">Expires: {new Date(job.expires_on).toLocaleDateString()}</p>
                    </div>
                ))}
            </div>
        </div>
    );
}